//branding portfolio
section.branding-portfolio {
	padding: $padding_section;
	background-color: #fff;

	.main-title {
		text-align: center;
		margin-bottom: 5rem;

		h2 {
			font-weight: 700;
			color: $main_color;
			text-transform: capitalize;
		}

		p {
			max-width: 650px;
			margin: auto;
			font-size: 18px;
			color: $dark_color;
		}
	}

	.content {
		display: grid;
		grid-template-columns: repeat(3, calc((100% / 3) - 30px));
		gap: 45px;

		.box {
			.box-image {
				margin-bottom: 15px;
				img {
					// height: 300px;
					width: 100%;
					// object-fit: cover;
					// object-position: top;
					border-radius: 15px;
				}
			}

			.box-title {
				h3 {
					color: $dark_color;
					text-transform: uppercase;
					font-size: 18px;
					font-weight: 600;
				}
			}
			.box-description {
				p {
					color: $dark_color;
					font-size: 14px;
				}
			}
		}
	}
}

//branding portfolio

//social media portfolio
section.social-media-portfolio {
	@extend .branding-portfolio;
	background-color: $dark_color;

	.main-title {
		text-align: center;
		margin-bottom: 5rem;

		p {
			color: #fff;
		}
	}

	.content {
		.box {
			.box-title {
				h3 {
					color: #fff;
					text-transform: uppercase;
					font-size: 18px;
					font-weight: 600;
				}
			}
			.box-description {
				p {
					color: #fff;
					opacity: 0.5;
					font-size: 14px;
				}
			}
		}
	}
}
//social media portfolio

//web portfolio
section.web-portfolio {
	@extend .branding-portfolio;
}
//web portfolio

//single portfolio
section.single-portfolio {
	padding: $padding_section;

	.main-image {
		margin-bottom: 40px;
		height: 100%;
		// max-height: 450px;
		display: block;

		img {
			@extend %fitimage;
			object-position: top;
			border-radius: 5px;
		}
	}

	.main-title {
		margin-bottom: 5rem;

		h2 {
			font-weight: 700;
			color: $main_color;
			text-transform: capitalize;
		}

		p {
			font-size: 14px;
			color: #fff;
		}
	}

	.content {
		.box {
			margin-bottom: 25px;
		}
		.box-image {
			height: 100%;
			// max-height: 450px;
			display: block;

			img {
				@extend %fitimage;
				object-position: center;
				border-radius: 5px;
				max-height: 800px;
			}
		}
	}
}
//single portfolio

//about me
section.about {
	.content {
		.icon {
			text-align: end;
			img {
				width: 500px;
			}
		}
		.text {
			display: flex;
			flex-direction: column;
			justify-content: center;
			color: #fff;
			height: 100%;

			h1 {
				font-size: 26px;
				font-weight: 600;
				text-transform: capitalize;
				opacity: 0.5;
			}

			p {
				font-size: 14px;
			}
		}
	}
}
//about me

//download cv
section.cv {
	padding: $padding_section;
	.content {
		text-align: center;

		.main-title {
			h2 {
				color: #fff;
				opacity: 0.4;
				font-size: 1.5rem;
				font-weight: 600;
			}
		}
		.icon {
			margin-bottom: 30px;
			img {
				width: 70px;
			}
		}
		.button {
			.btn {
				@extend %btn_selector;
				text-transform: uppercase;
			}
			img {
				width: 30px;
				margin-inline-start: 5px;
			}
		}
	}
}
//download cv

//reason behind
section.reason-behind {
	padding-bottom: 100px;

	.content {
		.main-title {
			h2 {
				color: #fff;
				opacity: 0.4;
				font-size: 1.5rem;
				font-weight: 600;
			}
		}
		.image {
			margin-bottom: 30px;
			text-align: center;
