				font-size: 14px;
			}
		}
	}
}
//reason behind

//links
section.links {
	padding: $padding_section;
	background-color: #fff;

	.content {
		text-align: center;

		.main-title {
			h2 {
				color: $main_color;
				font-weight: 800;
			}
			img {
				width: 30px;
				margin-inline-start: 10px;
			}
		}
		.text {
			margin-bottom: 40px;
		}
		.social-media {
			margin-bottom: 25px;

			a {
				color: $main_color;
				font-size: 40px;
				margin: 0 10px;
			}
		}
	}
}
//links

//contacts
section.contacts {
	padding: $padding_section;

	.content {
		text-align: center;

		.main-title {
			margin-bottom: 4rem;
			h2 {
				color: #fff;
				font-weight: 800;
				opacity: 0.5;
			}
			p {
				color: #fff;
			}
		}
		.info {
			display: flex;
			justify-content: space-between;

			.box {
				a {
					color: $main_color;
					margin: 0 10px;
					font-weight: 600;

					i {
						color: #fff;
						margin-inline-end: 5px;
					}
				}
			}
		}
	}
}
//contacts
