@media screen and (max-width: 1500px) {
}

@media screen and (max-width: 1199px) {
}

@media screen and (max-width: 991px) {
	header {
		.navbar {
			.social-media {
				display: none;
			}
		}
	}
	section.hero {	
		.content {	
			.logo {	
				img {
					&.text {
						width: 400px;
					}
					&.icon {
						width: 150px;
					}
				}
			}
		}
	}
	section.what-can {
		.content {
			display: block;

			.box {
				width: 100%;
				margin-bottom: 25px;
			}
		}
	}

	section.branding-portfolio .content,
	section.web-portfolio .content,
	section.social-media-portfolio .content {
		grid-template-columns: repeat(2, calc((100% / 2) - 23px));
	}
}

@media screen and (max-width: 767px) {
	header {
		.content {
			.desktop {
				display: none;
			}
			.mobile-menu-icon {
				display: block;
			}
		}
	}
	section.hero {	
		.content {	
			.logo {	
				img {
					&.text {
						width: 350px;
					}
					&.icon {
						width: 120px;
					}
				}
			}
		}
	}
	section.branding-portfolio .content,
	section.web-portfolio .content,
	section.social-media-portfolio .content {
		// display: block;
		grid-template-columns: repeat(1, auto);

		.box {
			// margin-bottom: 35px;

			.box-image {
				img {
					height: auto;
					max-height: 350px;
				}
			}
		}
	}

	section.about {
		padding-top: 70px;

		.content {
			.text {
				text-align: center;
			}
			.icon {
				img {
					max-width: 100%;
				}
			}
		}
	}

	section.reason-behind {
		.content {
			.text {
				text-align: start;
			}
			.image {
				padding-inline-start: 0;
			}
		}
	}
	section.contacts .content .info {
		display: block;
		.box {
			a {
				i {
					display: block;
					font-size: 20px;
					margin-bottom: 5px;
				}
			}
		}
	}
}

@media screen and (max-width: 670px) {
}

@media screen and (max-width: 575px) {
	section.hero {	
		.content {	
			.logo {	
				display: block;
			}
		}
	}
}

@media screen and (max-width: 500px) {
	section.hero {	
		.content {	
			.logo {
				margin-bottom: 75px;
	
				img {
					&.icon {
						width: 130px;
					}
					&.text {
						margin-inline-end: 0;
						margin-bottom: 30px;
					}
				}
			}
		}
	}
	section.what-can {
		.main-title {
			h2 {
				font-size: 16px;
				img {
					width: 160px;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	section.about-me {
		.content {
			.box {
				.box-title {
					h3 {
						font-size: 26px;
					}
				}
			}
		}
	}

	section.cta {
		.content {
			.main-title {
				h2 {
					font-size: 20px;
				}
			}
		}
	}
}
