//global style
.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "Montserrat", sans-serif;

	.main-wrapper {
		background-color: $dark_color;
		width: 100%;
	}
}

.fancybox__progress {
	background: $main_color;
}

//global style

//header
header {
	padding: 20px 0;

	&.mobile {
		.mobile-menu {
			top: 0;
		}
	}

	&.transparent {
		.content {
			.logo {
				img {
					&.dark {
						display: none;
					}

					&.light {
						display: block;
					}
				}
			}
		}
	}

	&.dark {
		background-color: $dark_color;
		.content {
			.logo {
				img {
					&.dark {
						display: none;
					}

					&.light {
						display: block;
					}
				}
			}
		}
	}

	&.light {
		background-color: #fff;

		.content {
			.logo {
				img {
					&.dark {
						display: block;
					}

					&.light {
						display: none;
					}
				}
			}
			.nav-item {
				a {
					color: $dark_color;
				}
			}

			.social-media {
				a {
					color: $dark_color;
				}
			}
		}
	}

	.navbar-collapse {
		justify-content: center;
	}
	.content {
		display: flex;
		justify-content: space-between;
		width: 100%;
		align-items: center;

		.logo {
			max-width: 200px;
		}

		.mobile-menu-icon {
			button {
				background-color: transparent;
				border: none;

				&:focus {
					outline: none;
					box-shadow: none;
				}
			}
			i {
				font-size: 28px;
				color: $main_color;
				transform: scaleX(1.5);
			}
		}

		.nav-item {
			a {
				color: #fff;
				text-transform: capitalize;
				margin: 0 5px;
			}
			&.active {
				a {
					color: $main_color;
				}
			}
		}

		.social-media {
			a {
				color: #fff;
				font-size: 20px;
				margin: 0 5px;
			}
		}
	}

	.offcanvas {
		height: 100vh;
	}
	.mobile-menu {
		background-color: $dark_color;
		width: 100%;
		height: 100%;
		padding: 25px;
		text-align: center;
		transition: all ease 0.3s;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		.logo {
			margin-bottom: 50px;

			img {
				width: 450px;
			}
		}

		.links {
			a {
				color: #fff;
				text-transform: capitalize;
				margin: 15px 0;
				display: block;
				font-size: 30px;

				.separ {
					display: block;
				}
			}
		}

		.social-media {
			a {
				color: #fff;
				font-size: 28px;
				margin: 0 10px;
				display: block;
			}
		}

		.close {
			// position: absolute;
			// bottom: 0;
			// right: 0;
			text-align: end;

			button {
				color: $main_color;
				font-size: 28px;
				font-weight: 700;
				padding: 0;
				background-color: transparent;
				border: 5px solid #fff;
				width: 45px;
				height: 45px;
				border-radius: 50%;
				line-height: 0;
			}
		}
	}
}

//header

//main section
section.hero {
	padding: 150px 0 80px;

	.content {
		text-align: center;

		.logo {
			margin-bottom: 75px;
			display: flex;
			justify-content: center;

			img {
				&.icon {
					width: 200px;
				}
				&.text {
					width: 600px;
					margin-inline-end: 30px
				}
			}
		}

		.button {
			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//main section

//what can
section.what-can {
	padding: $padding_section;

	.main-title {
		text-align: center;
		margin-bottom: 4rem;

		h2 {
			color: #fff;
			font-size: 26px;
			display: flex;
			align-items: end;
			justify-content: center;

			img {
				margin: 0 5px;
			}
		}
	}

	.content {
		display: flex;
		justify-content: space-between;

		.box {
			width: calc((100% / 3) - 50px);
			text-align: center;
			padding: 30px;
			background-color: #fff;

			.box-icon {
				margin-bottom: 25px;

				img {
					height: 100px;
					width: 140px;
					object-fit: content;
				}
			}

			.box-title {
				color: $main_color;
				text-transform: uppercase;
				h3 {
					font-size: 22px;
					font-weight: 600;
				}
			}
		}
	}

	.works-title {
		text-align: center;
		margin-top: 10rem;

		h2 {
			color: #fff;
			text-transform: uppercase;
		}
	}
}

//what can

//works
section.works {
	padding: $padding_section;
	background-color: #fff;

	.content {
		.box {
			position: relative;
			margin-bottom: 25px;
			z-index: 1;

			// &::after {
			// 	content: "";
			// 	position: absolute;
			// 	left: 0;
			// 	top: 0;
			// 	width: 100%;
			// 	height: 100%;
			// 	background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
			// 	z-index: -1;
			// }

			.box-image {
				img {
					// height: 270px;
					// object-fit: cover;
					width: 100%;
				}
			}

			.box-title {
				display: flex;
				align-items: flex-end;
				position: absolute;
				bottom: 0;
				left: 0;
				top: 0;
				right: 0;
				padding-inline-start: 10px;
				background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
				z-index: 1;

				h3 {
					display: flex;
					align-items: center;
					color: #fff;
					text-transform: uppercase;
					font-size: 16px;
				}

				img {
					width: 17px;
					margin-inline-start: 10px;
				}
			}
		}

		.button {
			text-align: center;
			padding-top: 30px;

			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//works

//about me
section.about-me {
	padding: $padding_section;

	.main-title {
		text-align: center;
		color: #fff;
		margin-bottom: 7rem;

		h2 {
			opacity: 0.6;
			font-weight: 700;
		}

		p {
			max-width: 550px;
			margin: auto;
			font-size: 14px;
		}
	}

	.content {
		.box {
			margin-bottom: 25px;
			text-align: center;

			.box-icon {
				margin-bottom: 25px;

				img {
					height: 200px;
					object-fit: contain;
					width: 100%;
				}
			}

			.box-title {
				h3 {
					color: #fff;
					text-transform: uppercase;
					font-size: 36px;
					font-weight: 700;

					span {
						color: $main_color;
					}
				}
			}
		}
	}
}

//about me

//call to action
section.cta {
	margin-bottom: 7rem;

	.content {
		background-color: #474747;
		padding: 25px;
		text-align: center;

		.main-title {
			img {
				width: 70px;
				margin-bottom: 10px;
			}

			h2 {
				color: #fff;
				font-weight: 700;
				font-size: 40px;
				max-width: 800px;
				margin: auto;
			}

			p {
				color: #fff;
				opacity: 0.5;
				font-size: 18px;
				text-transform: uppercase;
			}
		}

		.button {
			text-align: center;
			padding-top: 30px;

			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//call to action

//footer
footer {
	padding: 50px 0 30px;
	background-color: #fff;

	&.dark {
		background-color: $dark_color;

		.content {
			.footer-logo {
				img {
					&.dark {
						display: none;
					}

					&.light {
						display: block;
					}
				}

				p {
					color: #fff;
				}
			}

			.social-media {
				a {
					color: #fff;
				}
			}

			.copyrights {
				p {
					color: $main_color;
				}
			}
		}
	}

	&.light {
		background-color: #fff;

		.content {
			.footer-logo {
				img {
					&.dark {
						display: block;
					}

					&.light {
						display: none;
					}
				}
			}
		}
	}

	.content {
		text-align: center;

		.footer-logo {
			img {
				margin-bottom: 30px;
			}

			p {
				font-size: 14px;
			}
		}

		.footer-links {
			margin-bottom: 25px;

			a {
				color: $main_color;
				text-transform: capitalize;
				margin: 0 5px;
			}
		}

		.social-media {
			margin-bottom: 25px;

			a {
				color: $dark_color;
				font-size: 20px;
				margin: 0 5px;
				transition: all ease 0.3s;

				&:hover {
					color: $main_color;
				}
			}
		}

		.copyrights {
			text-align: center;

			p {
				margin-bottom: 0;
				color: $main_color;
			}
		}
	}
}

//footer
